.main-match-details-container {
    height: 53vh;
    width: 100%;
    padding: 5% 5%;
    border-top: 7px solid #FAFAFA;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
}

.main-match-details-title {
    font-family: Bebas Neue;
    font-size: 1.3rem;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    margin: 0;
    margin-bottom: .5vh;
}

.main-match-details-result {
    margin: 0;
    color: #150000;
    font-size: 1rem;
    font-weight: 500;
    margin-bottom: 4vh;
    line-height: 1rem;
    text-align: center;
}