.fixture-switcher-container {
    height: 8vh;
    width: 100%;
    margin-top: .5vh;
    margin-bottom: 1vh;
    display: flex;
    flex-direction: row;
    gap: 25px;
    align-items: center;
    justify-content: flex-start;
}

.fixture-switcher-box {
    height: 70%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: 1rem;
    font-weight: 600;
    border: none;
    color: #5B5757;
    padding: 0;
}

.fixture-switcher-box:hover {
    cursor: pointer;
}

.fixture-switcher-box.selected {
    padding-top: 2px;
    border-bottom: 2px solid #FF5050;
    color: black;
}

@media screen and (max-width: 480px) {

    .fixture-switcher-box {
        height: 65%;
    }

}