.MatchPage {
    height: auto;
    width: 100vw;
    padding: 2% 5%;
    padding-bottom: 10%;
}

.MatchPageLoading {
    height: 100vh;
    width: 100vw;
    padding: 5% 5%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.loader {
    border: 4px solid rgba(0, 0, 0, 0.1);
    width: 36px;
    height: 36px;
    border-radius: 50%;
    border-left-color: #09f;
    animation: spin 1s ease infinite;
}

@keyframes spin {
    0% {
      transform: rotate(0deg);
    }
  
    100% {
      transform: rotate(360deg);
    }
}

.nav-match {
    height: 4vh;
    width: 100%;
}

.nav-match-icon {
    height: 100%;
}

.header-match {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    margin: 2vh 0;
}

.match-round {
    font-family: Bebas Neue;
    font-size: 1.5rem;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    margin: 0;
    margin-bottom: .7vh;
}

.match-datetime {
    color: #5B5757;
    font-size: 1rem;
    font-weight: 600;
    margin: 0;
    margin-bottom: 2vh;
}

.header-match-container {
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}

.header-match-home-team, .header-match-away-team {
    height: 100%;
    width: 27%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.header-match-result {
    height: 100%;
    width: 40%;
    display: flex;
    flex-direction: column;
    align-items: stretch;
    justify-content: center;
    gap: 1vh;
}

.header-match-score {
    height: 30%;
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}

.header-match-penalty {
    color: #848181;
    font-size: .8rem;
}

.header-match-goals-box {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}

.header-match-goals {
    margin: 0;
    color: #150000;
    font-size: 1.2rem;
    font-weight: 600;
}

.header-match-info {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.header-match-info-penalty {
    margin: 0;
    color: #848181;
    font-size: .8rem;
    font-weight: 500;
    text-align: center;
    line-height: .9rem;
}

.header-match-info-status {
    margin: 0;
    color: #150000;
    font-size: .9rem;
    font-weight: 500;
}

.header-match-logo-box {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: stretch;
    justify-content: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-bottom: 1.5vh;
}

.header-match-team-logo {
    object-fit: contain;
    max-height: 90%;
    max-width: 90%;
}

.header-match-team-name {
    margin: 0;
    color: #150000;
    font-size: .9rem;
    font-weight: 600;
    overflow-wrap: break-word;
    line-height: 1rem;
    text-align: center;
}

.main-match {
    height: auto;
    width: 100%;
    background-color: #FFF;
}

@media screen and (max-width: 480px) {

    .MatchPage {
        padding: 5% 5%;
    }

    .header-match-container {
        height: 18vh;
        align-items: stretch;
    }

    .main-match {
        height: 60vh;
    }

}